import React from 'react';

import { Route, Switch } from 'react-router-dom';

import TypeFaceContainer from './components/style-guide/Typeface/Typeface';
import PaletteContainer from './components/style-guide/Palette';
import ElementsContainer from './components/style-guide/Elements';
import ResultContainer from './components/style-guide/Result/Result';

export default function Routing() {
  return (
    <Switch basename={'/style-guide'}>
      <Route exact path="/" component={TypeFaceContainer} />
      <Route path="/style-guide/typeface" component={TypeFaceContainer} />
      <Route path="/style-guide/palette" component={PaletteContainer} />
      <Route path="/style-guide/elements" component={ElementsContainer} />
      <Route
        path={['/style-guide/result/:id', '/style-guide/result/']}
        component={ResultContainer}
      />
    </Switch>
  );
}
